import React, { Component } from 'react';
import { Link } from "react-router-dom";
export default class Footer extends Component {
    render() {
        return (
            <div className="footerWeb">
                 <div className="footerWeb__lContent">
                    <div className="lContent__story">
                        <Link to="/story">
                            <p className="story__text">story.</p>
                        </Link>
                    </div>
                </div>
                <div className="footerWeb__rContent">
                    <div className="rContent__project">
                        <Link to="/project">
                            <p className="project__text">project.</p>
                        </Link>
                    </div>
                </div>
            </div>
        );
    }
}