import React, { Component } from 'react';
import { Link } from "react-router-dom";
export default class Header extends Component {
    render() {
        return (
            <div className="headerWeb">
                <div className="headerWeb__lContent">
                    <div className="lContent__logo">
                        <Link to="/">
                            <img className="logo__img" src="images/logo/logo.png" alt="" />
                        </Link>
                    </div>
                    <div className="lContent__sound">
                        <svg width="21" height="26" className="sound__soundBar">
                            <line x1="2" y1="1" x2="12" y2="1" className="soundBar__item"></line>
                            <line x1="2" y1="5" x2="16" y2="5" className="soundBar__item"></line>
                            <line x1="2" y1="9" x2="14" y2="9" className="soundBar__item"></line>
                            <line x1="2" y1="13" x2="20" y2="13" className="soundBar__item"></line>
                            <line x1="2" y1="17" x2="16" y2="17" className="soundBar__item"></line>
                            <line x1="2" y1="21" x2="13" y2="21" className="soundBar__item"></line>
                        </svg>
                    </div>
                </div>
                <div className="headerWeb__rContent">
                    <div className="rContent__contact">
                        <Link to="/contact">
                            <p className="contact__text">contact.</p>
                        </Link>
                    </div>
                </div>
            </div>
        );
    }
}