import React, { Component } from 'react';
export default class Cursor extends Component {
    render() {
        return (
            <div className="cursor">
                <div className="cursor__wrap">
                    <div className="cursor__point">
                        <div className="point__default"></div>
                    </div>
                    <div className="cursor__border">
                        <div className="border__oval"></div>
                    </div>
                </div>
            </div>
        );
    }
}